export const mutations = {
	removeMessage (state, { message }) {
		state.messages.splice(state.messages.indexOf(message), 1)
	},
	markAsRead (state, message) {
		let _message = state.messages.filter((k) => {
			return k.id === message.id
		});
		_message[0].unreaded = false;
	},
	toggleMessage (state, params) {
		let message = state.messages.filter((k) => {
			return k.id === params.id
		});
		message[0].selected = params.value;
	},
	toggleMessages (state, value) {
		state.messages.forEach((k) => {
			k.selected = value;
		});
	}
};

export const getters = {
	messages (state) {
		return state.messages
	},
	messageIDs (state) {
		return state.messages.map(obj => {
			return obj.id
		})
	},
	tags (state) {
		return state.tags
	}
};
